import { createSlice } from '@reduxjs/toolkit';
import {RootState} from "./store";

interface BpmState {
  value: number;
}

const initialState : BpmState = {
  value: 60,
};

export const bpmSlice = createSlice({
  name: 'bpm',
  initialState,
  reducers: {
    setBpm: (state, action) => {
      const bpm = parseFloat(action.payload);
      if (typeof bpm === 'number') {
        state.value = bpm;
      }
    }
  },
});

export const { setBpm } = bpmSlice.actions;
export const  selectBpm = (state : RootState) => parseFloat(state.bpm.value.toFixed(1));

export default bpmSlice.reducer;
