import React, {useEffect} from 'react';
import styles from './TapArea.module.css';
import { useDispatch, useSelector } from 'react-redux';
import {selectBpm} from '../../app/bpmSlice';

let date = null;
const errorAmount = 2;

export function TapArea() {
  const bpm = useSelector(selectBpm);
  useEffect(function () {
    const canvas = document.querySelector('#glCanvas');
    const ctx = canvas.getContext("2d");
    const center = canvas.width / 2;
    ctx.fillStyle = "red";
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    ctx.textAlign = 'center';
    ctx.fillStyle = "white";
    ctx.font = "30px Comic Sans MS";
    ctx.fillText("TAP", center, 50);

    const canvasWrapper = document.getElementById("canvas-wrapper");
    const canvasSection = document.getElementById("canvas-section");
    canvasSection.addEventListener('mousedown', function() {
      if (date) {
        let myDate = Date.now() - date;
        date = Date.now();
        let seconds = myDate / 1000
        let myBpm = 60 / seconds;
        let difference = bpm - myBpm;
        let helpString = '';
        console.log("difference", difference);
        if (difference > errorAmount) {
          helpString = "TAP FASTER";
          ctx.fillStyle = "red";
          canvasWrapper.style.background = "red";
        } else if  (difference < -1 * errorAmount) {
          helpString = "SLOW DOWN";
          ctx.fillStyle = "red";
          canvasWrapper.style.background = "red";
        } else {
          helpString = ":)";
          ctx.fillStyle = "green";
          canvasWrapper.style.background = "green";
        }
        ctx.fillRect(0, 0, canvas.width, canvas.height);

        ctx.fillStyle = "white";
        ctx.font = "30px Comic Sans MS";
        ctx.fillText(helpString, center, 50);
      } else {
        date = Date.now();
      }
    }, false);
  });
  return <section id="canvas-section" className={styles.section}>
    <div id="canvas-wrapper" className={styles.canvasWrapper}>
      <canvas
          className={styles.canvas}
          onMouseDown={()=>{}}
          id="glCanvas" />
    </div>
  </section>
}
