import React, {useState} from 'react';
import * as Tone from 'tone'

import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';
import Button from "@mui/material/Button";

import {useAppDispatch, useAppSelector} from "../../app/hooks";
import {selectBpm, setBpm} from "../../app/bpmSlice";
import {Seconds} from "tone/Tone/core/type/Units";
import styles from "./Playback.module.css"

import quarterNote from './quarter-note.png'
import sixteenthNote from './16th.svg'
import eightNote from './8th.png'
import eightNoteTriplet from './8th-triplet.webp';
import sixteenthNoteTriplet from './sixteenth-note-triplet.png'

type Subdivision = "4n" | "8n" | "16n" | "8t" | "16t";

let clickSynth: Tone.Synth;
let loopClick: Tone.Loop | null;

function start() {
    if (!loopClick) {
        clickSynth = new Tone.Synth({
            oscillator: {
                type: 'sine',
            },
            envelope: {
                attack: 0,
                decay: 0.1,
                sustain: 0,
                release: 0.1,
            }
        }).toDestination();
        loopClick = new Tone.Loop(click, '4n');
        Tone.Transport.start();
        loopClick.start(0);
    }
}

function stop() {
    if (loopClick) {
        loopClick.stop();
        loopClick = null;
    }
    if (loopBeat) {
        loopBeat.stop();
        loopBeat = null;
    }
}

function click(time: Seconds) {
    clickSynth.triggerAttackRelease(440, '8n', time);
}

let bassSynth: Tone.MembraneSynth, loopBeat: Tone.Loop | null;

function startSubdivision(interval: string) {
    if (loopBeat) {
        loopBeat.stop();
        bassSynth = new Tone.MembraneSynth().toDestination();
        loopBeat = new Tone.Loop(subdivision, interval);
        Tone.Transport.start();
        loopBeat.start(0);
    }
    if (!loopBeat) {
        bassSynth = new Tone.MembraneSynth().toDestination();
        loopBeat = new Tone.Loop(subdivision, interval);
        Tone.Transport.start();
        loopBeat.start(0);
    }
}

function subdivision(time: Seconds) {
    bassSynth.triggerAttackRelease('c1', '8n', time);
}

function stopSubdivision() {
    if (loopBeat) {
        loopBeat.stop();
        loopBeat = null;
    }
}

function setTransportBpm(bpm: number) {
    Tone.Transport.bpm.value = bpm;
}

export function Playback() {
    const bpm = useAppSelector(selectBpm);
    const [subdivision, setSubdivision] = useState<Subdivision>('4n');
    const [play, setPlay] = useState(false);

    const handleIntervalChange = (
        event: React.MouseEvent<HTMLElement>,
        newInterval: Subdivision,
    ) => {
        if (newInterval) {
            setSubdivision(newInterval);
            if (newInterval != "4n") {
                startSubdivision(newInterval);
            } else {
                stopSubdivision();
            }
        }
    };

    const handlePlayChange = (
        event: React.MouseEvent<HTMLElement>,
        play: boolean,
    ) => {
        setPlay(play);
        if (play) {
            start();
        } else {
            stop();
        }
    };

    setTransportBpm(bpm);

    return <section className={styles.toggleButtonGroup}>
                <ToggleButtonGroup
                    exclusive
                    orientation="vertical"
                    value={play}
                    onChange={handlePlayChange}
                >
                    <ToggleButton value={true}>
                        <span className="material-symbols-outlined">play_circle</span>
                    </ToggleButton>
                    <ToggleButton value={false}>
                        <span className="material-icons">stop_circle</span>
                    </ToggleButton>
                </ToggleButtonGroup>
    </section>;
}